<template>
  <div class="myServicesList">
    <div class="page-header-main">
      <mt-header>
        <a @click="back" slot="left">
          <mt-button icon="back">服务列表</mt-button>
        </a>
      </mt-header>
    </div>
    <div class="serviecsList">
      <div class="recommendsService" v-for="service in myServicesList">
          <div class="myServicesListItem">
            <div @click="handleClick(service.id,service.title, service.price,service.duration)">
              <p class="title">{{service.title}}</p>
            </div>
            <div class="info">
              <p class="day">{{service.price}}元/天</p>
              <p class="showDetail" @click="showDetailFn($event, service.title)">查看详情</p>
            </div>
          </div>
          <div class="recommendsServiceDetail" :ref="service.title"> 
            <ul>
              <li v-for="desc in service.desc">{{desc}}</li>
            </ul>
          </div>
        </div>
    </div>
  </div>
</template>
  </div>
<script>
import { mapGetters, mapActions} from 'vuex'
export default {
  name: 'jrServicesList',
  methods: {
    ...mapActions({
      fetchMyServicesList: 'fetchMyServicesList',
      changeCompanyType: 'changeCompanyType'
    }),
    back: function() {
      history.go(-1);
    },
    handleClick: function(id, title, price,duration) {
      this.$router.push({path: '/appointment', query: { id: id, type: 'my', duration: duration }})
      this.changeCompanyType(title + ' ' + price);
    },
    showDetailFn: function(e, name) {
      if(e.target.style.backgroundImage == '' || e.target.style.backgroundImage == 'url("/static/detailDown.png")') {
        this.$refs[name][0].style.display = 'block';
        e.target.style.backgroundImage = 'url("/static/detailUp.png")';
      } else {
        this.$refs[name][0].style.display = 'none';
        e.target.style.backgroundImage = 'url("/static/detailDown.png")';
      }
    }
  },
  computed: {
    ...mapGetters({
      globalVal: 'getGlodalVal',
      myServicesList: 'getMyServices'
    })
  },
  created() {
    this.fetchMyServicesList(this.globalVal.currentCity).catch(error => {
      if(error.body.code == 3001) {
        this.$router.push('/login');
      }
    });
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.myServicesList .mint-button-text {
  padding-right: 0.5rem;
}
.myServicesList {
  background-color: #e3e1e3;
}

.myServicesList .serviecsList {
  font-size: 0.4rem;
  background: #e3e1e3;
}
.myServicesList .recommendsService {
  margin: 0.2rem 0;
}
.myServicesList .myServicesListItem {
  display: flex;
  display: -ms-flexbox;
  justify-content: space-between;
  height: 1.5rem;
  background-color: #fff;
  border-left: 0.3rem solid #ff6990;
}
.myServicesList .othersService {
  margin: 0.2rem 0;
}
.myServicesList .othersServiceItem {
  display: flex;
  display: -ms-flexbox;
  justify-content: space-between;
  height: 1.5rem;
  background-color: #fff;
}
.myServicesList .othersServiceItem {
  background-color: #fff;
}
.myServicesList .title{
  height: 1.5rem;
  line-height: 1.5rem;
  margin-left: 0.4rem;
  color: #565256;
}
.myServicesList .info {
  margin-right: 0.4rem;
  min-width: 1.68rem;
}
.myServicesList .info .day {
  line-height: 1rem;
  height: 1rem;
  color: #ff8f0b;
}
.myServicesList .info .showDetail {
  font-size: 0.3rem;
  color: #565256;
  background-size: contain;
  background-image: url(../assets/detailDown.png);
  background-repeat: no-repeat;
  background-position: right;
}
.myServicesList .recommendsServiceDetail {
  display: none;
  margin-left: 0.4rem;
}
.myServicesList li span {
  width: 0.2rem;
  height: 0.2rem;
  background: #fa73ae;
  display: inline-block;
  border-radius: 0.1rem;
  vertical-align: middle;
  margin-right: 0.3rem;
}
.myServicesList li {
  list-style: none;
  height: 0.7rem;
  line-height: 0.7rem;
  color: #a19ba1;
  font-size: 0.3rem;
}
.myServicesList .difftitle {
  height: 1.5rem;
  line-height: 1.5rem;
  margin-left: 0.4rem;
  color: #565256;
}
.myServicesList .othersServiceDetail {
  display: none;
  margin-left: 0.4rem;
}
</style>



